.challengeModalMain {
  visibility: hidden;
  opacity: 0;
  width: 100vw;
  top: 10vh;
  right: 0;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
  position: fixed;
  z-index: 10;
}
@keyframes fadeOut {
  0% {
    visibility: visible;
    opacity: 1;
  }
  100% {
    visibility: hidden;
    opacity: 0;
  }
}

.modalCard{
    visibility: hidden;
  opacity: 0;
  width: 30vw;
  display: flex;
  flex-direction: column;
  justify-self: center;
  align-self: center;
  position: fixed;
  top:25vh;
  transform: scale(1);
  height:auto;
  left:35vw;
  transition: all .5s;
  z-index: 11;
    border: 1px solid #9b0000;
    box-shadow: #9b0000 0px 0px 20px;
    border-radius: 20px;
    background-color: rgba(0, 0, 0, 0.95);
}
.active {
    visibility: visible;
    transition: opacity 0.5s;
    opacity: 1;
    animation: none;
    
  }

.modalCard.active{
  visibility: visible;
    transition: all 0.4s;
  transform: scale(1.2);
    opacity: 1;
    top:30vh;
    animation: none;
}
.modal-header{
  height: 4rem;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-items: center;
  border-bottom: 2px solid #9b0000;
}

.modal-body{
  font-size: small;
  height: auto;
}
.modal-flag{
  min-height: 10px;
  width: 100%;
  border-top: 1px solid #9b0000;
}
.modalChallengeName{
  color:white;
  font-size: x-large;
  font-weight: 500;
  margin-top: 10px;
}
.modalChallengePoints{
  margin-top: -20px;
  color: white;
}
.modalChallengePoints::after{
  content:" Puan"
}
.modalDesc{
  color: white;
  padding: 10px 20px;
  overflow-wrap: break-word;
  
}
.button-group{
  display: flex;
  flex-direction: row;
  width: 80%;
  margin: 0 auto;
  padding-top: calc(6vh - 35px);
}
.flagInput{
  width: 80%;
  display:inline-block ;
  height: 30px;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}
.flagInput::placeholder{
  font-size: 1rem;
  margin: 1rem;
}
button.flagSubmit {
  transition: box-shadow 0.2s;
  outline: none;
  width: 20%;
  padding-right: 6%;
  height: 34px;
  border: 1px solid #4b0000;
  background-color: rgba(0, 0, 0.3);
  border-radius: 20px;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
  color: white;
  padding-left: 10px;
}
button.flagSubmit::placeholder {
  color: rgba(255, 255, 255, 0.7);
}

button.flagSubmit:focus {
  transition: box-shadow 0.2s;
  border: 1px solid #9b0000;
  box-shadow: 0px 0px 20px #fb0000;
}
button.flagSubmit:hover {
  box-shadow: 0px 0px 20px #fb0000;
  transition: box-shadow 0.2s;
}
.wrongAnswer{
  color: red;
  width: 100%;
  text-align: center;
  margin-top: 0;
}