#challenges-main{
    min-height: 100vh;
    width: 100%;
    background-size: cover;
    position: relative; /* Make sure the container is positioned */
    width: 100%; /* Set the desired width */
    height: 400px; /* Set the desired height */
    overflow-x: hidden;
    background-color: rgba(0, 0, 0, 0.7);
}

#challenges-main::before{
  background-image: url('/public/images/challengesBG.jpg');
    min-height: 100vh;
    width: 100%;
    background-attachment: fixed; /* Adjust to your needs */
    position: fixed;
    background-size: cover;
  content: "";
    z-index: -1;

}
#challengeHeader{
    color:#9b0000;
    font-size: 3rem;
    text-align: center;
    padding-top: 15vh;
}
.challenge-grid {
  margin-top: 150px;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  padding-bottom: 100px;
  grid-gap: 100px; /* You can adjust the gap between grid items as needed */
}