.challenge-card-body{
    width: 350px;
    background-color: rgba(0, 0, 0, 0.5);
    height: 200px;
    display: flex;
    flex-direction: column;
    justify-self: center;
    border-radius: 40px;
    border: 1px solid #9b0000
}

@keyframes glow {
    0%{
        box-shadow: #9b0000 0px 0px 10px;
    }
    50%{
        box-shadow: #9b0000 0px 0px 40px;
    }
    100%{
        box-shadow: #9b0000 0px 0px 10px;
    }
}

.challenge-card-top{
    width: 100%;
    height: 140px;
    border-bottom: 2px solid #6b0000;
}
.point{
    font-size: larger;
    color: white;
    width: 100%;
    justify-content: center;
    display: flex;
    margin-top: -25px;
}

.challenge-name{
    color:white;
    font-size: xx-large;
    text-align: center;
}
.challenge-card-bottom{
    height: 60px;
    width: 100%;
    background-color: rgba(255,0,0,0.1);
    color:white;
    font-size: x-large;
    border: none;
    border-bottom-left-radius: 40px;
    border-bottom-right-radius: 40px;
    cursor: pointer;
}
.challenge-card-bottom[isDisabled="true"]{
    pointer-events: none;
}
.challenge-card-bottom[isDisabled="false"]{
    pointer-events: all;
    transition: background-color .2s;
    background-color:rgba(255,0,0,0.1)
}
.challenge-card-bottom[isDisabled="false"]:hover{
    transition: background-color .2s;
    background-color:rgba(255,0,0,0.2)
}